export default {
	data() {
		return {
			imageURL:'../../../static/ysy/photo.png',
			data:{
			}
		}
	},
	onLoad(){
		let params = {
		};
		let url = "/api/user";
		this.util.request(url, "POST", params, (res) => {
			console.log(JSON.stringify(res));
			if (res.statusCode == 200) {
				if (res.data.code == 1) {
					this.data = res.data.data;
				} else {
					this.util.showWindow(res.data.msg);
				}
			} else {
				this.util.showWindow("请求错误");
			}
		});
	},
	methods: {
		fuwuyufankui(){
			uni.navigateTo({
				url:"../fuwufankui/fuwufankui"
			})
		},
		pingtaixieyi(){
			uni.navigateTo({
				url:"../pingtaixieyi/pingtaixieyi"
			})
		},
		wentiyufankui(){
			uni.navigateTo({
				url:"../questionback/questionback"
			})
		},
		gouwuxuzhi(){
			uni.navigateTo({
				url:"../gouwuxuzhi/gouwuxuzhi"
			})
		},
		lianxiwomen(){
			uni.navigateTo({
				url:"../lianxiwomen/lianxiwomen"
			})
		},
		goorders(index) {
			this.util.tabCurrentIndex = index;
			uni.switchTab({
				url: "../../order/index/index"
			})
		},
		setinfo(){
			uni.navigateTo({
				url:"../set/set"
			})
		}
	}
}
